*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-image: url(./pages/assets/background-stars.svg);
    background-color: #070724;
}
.nav{
    background-color: #070724;
    color: white;
    display:flex ;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    height: 70px;
    background-image: url(./pages/assets/background-stars.svg);
}
.nav a{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}
.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}
.nav li:hover{
    background-color: #777;
}
.Name{
    font-size: 2rem;
}
hr{
    background: #FFFFFF;
    mix-blend-mode: normal;
    opacity: 0.2;
}
